/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  z-index: 1000;
  display: none;
}

.modal-bg {
  position: fixed;
  background: rgba(0, 0, 0, 0.6);
  width: 100vw;
  height: 100%;
}

.modal-box {
  position: relative;
  position: absolute;
  z-index: 10;
  width: 100%;
  max-width: 1080px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  /* .is-modal-relative &{
		position: absolute;
		transform: translate(-50%,0);
		top:30px;
	} */
}

.modal-box:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 56.25%;
}

.modal-box > .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.modal-layer,
.modal-video,
.modal-video__cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  box-sizing: border-box;
  padding: 0 60px;
}

@media all and (max-width: 640px) {
  .modal-layer,
  .modal-video,
  .modal-video__cover {
    padding: 0 6.25vw;
  }
}

.modal-video,
.modal-video__cover {
  height: auto;
}

.modal-btn__close {
  position: absolute;
  z-index: 1000;
  right: 60px;
  top: 0;
  transform: translate(50%, -50%);
}

@media all and (max-width: 640px) {
  .modal-btn__close {
    width: 9.375vw;
    right: 9.375vw;
  }
}

.is-video-stop .modal-video {
  display: none;
}

.modal-video__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  z-index: 100;
  display: none;
}

.is-video-stop .modal-video__cover {
  display: block;
}

.modal-video__cover--img {
  width: 100%;
  height: auto;
  cursor: pointer;
}

.modal-video__btn {
  display: block;
  position: absolute;
  width: 6.25%;
  max-width: 60px;
  height: auto;
  top: 50%;
  left: 50%;
